.header-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 25px;
    border-bottom: 1px solid #818181;
}

.header-logo {
    font-size: 24px;
    font-family: var(--bagel-font);
}

.header-menus {
    display: flex;
    gap: 10px;
    font-size: 16px;
    text-align: center;
}

.header-menu {
    width: 80px;
    font-weight: 400;
    cursor: pointer;
}

.header-menu-selected {
    width: 80px;
    font-weight: 900;
    cursor: pointer;
}






/* 햄버거 메뉴 스타일 */
.hamburger-menu {
    display: none; /* 기본적으로 햄버거 버튼은 숨김 */
    cursor: pointer;
}

.hamburger-menu .bar {
    width: 25px;
    height: 3px;
    background-color: #333;
    margin: 5px 0;
}

/* 모바일 메뉴 (햄버거 메뉴 열렸을 때) */
.mobile-menu {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* 모바일 화면에서 메뉴바 숨기고 햄버거 버튼 표시 */
@media (max-width: 768px) {
    .header-menus {
        display: none; /* 메뉴바 숨기기 */
    }

    .hamburger-menu {
        display: block; /* 햄버거 버튼 보이기 */
    }

    .mobile-menu {
        display: flex;
        position: absolute;
        top: 60px; /* 메뉴바 아래에 위치 */
        right: 0;
        background-color: #fff;
        padding: 20px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    }
}
