:root {
    /* Fonts */
    --goun-font: 'GowunBatang-Regular';
    --ridi-font: 'RIDIBatang';
    --onepick-font: 'YOnepickTTF-Bold';
    --jeju-font: 'EF_jejudoldam';
    --neo-font: 'NanumSquareNeo';
    --bagel-font: 'BagelFatOne-Regular';
    --apple-font: 'Mungyeong-Gamhong-Apple';
    --aggro-font: 'SBAggroB';
    
    /* Colors */
    --white: #ffffff;
    --black: #000000;
    --primary: #2596be;
    --error100: #ffbeba;
    --error200: #ff7c75;
    --error300: #d30b00;
    --warning100: #fff2aa;
    --warning200: #ffe455;
    --warning300: #b39600;
    --secondary100: #e7ebef;
    --secondary200: #dae1e7;
    --secondary300: #ced7de;
    --secondary400: #c2cdd6;
    --secondary500: #b6c3cd;
    --secondary600: #92a5b5;
    --secondary700: #7991a4;
    --secondary800: #637d92;
    --secondary900: #4a5e6d;
    --secondary1000: #323f49;
}

/* Font */

/* 고운바탕 - https://noonnu.cc/font_page/733 */
@font-face {
    font-family: 'GowunBatang-Regular';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/GowunBatang-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

/* 나눔스퀘어네오 - https://noonnu.cc/font_page/1053 */
@font-face {
    font-family: 'NanumSquareNeo';
    src: url(https://hangeul.pstatic.net/hangeul_static/webfont/NanumSquareNeo/NanumSquareNeoTTF-bRg.eot);
    src: url(https://hangeul.pstatic.net/hangeul_static/webfont/NanumSquareNeo/NanumSquareNeoTTF-bRg.eot?#iefix) format("embedded-opentype"), url(https://hangeul.pstatic.net/hangeul_static/webfont/NanumSquareNeo/NanumSquareNeoTTF-bRg.woff) format("woff"), url(https://hangeul.pstatic.net/hangeul_static/webfont/NanumSquareNeo/NanumSquareNeoTTF-bRg.ttf) format("truetype");
}

/* 티머니둥근바람 - https://noonnu.cc/font_page/458 */
@font-face {
    font-family: 'TmoneyRoundWindExtraBold';
